@font-face {
  font-family: "DaxlinePro";
  src: url("../fonts/DaxlinePro-Light.woff2") format("woff2"),
    url("../fonts/DaxlinePro-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "DaxlinePro";
  src: url("../fonts/DaxlinePro-Regular.woff2") format("woff2"),
    url("../fonts/DaxlinePro-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "DaxlinePro";
  src: url("../fonts/DaxlinePro-Bold.woff2") format("woff2"),
    url("../fonts/DaxlinePro-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"),
    url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"),
    url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
